// @flow

import styled, {css} from 'styled-components'
import {Toolbar, Typography} from '@material-ui/core'
import {Link} from 'gatsby'

import {SearchResult} from '../components'

export const MyToolbar = styled(Toolbar)`
  border-bottom: 1px solid ${props => props.theme.palette.divider};
  display: flex;
  align-items: center;
  justify-content: center;
`

export const SecondaryToolbar = styled(Toolbar)`
  justify-content: space-between;
  overflow-x: auto;
`

export const Space: typeof styled.div = styled.div`
  flex: 1;
`

export const Title: typeof Typography = styled(Typography).attrs({
  component: 'h2',
  variant: 'h5',
  color: 'inherit',
  align: 'center',
  noWrap: true,
})`
  position: absolute;
`

export const HeaderLink = styled(Link)`
  flex: 1;
  text-decoration: none;
  color: inherit;
`

export const MyLink = styled(Link)`
  padding: 8px;
  flex-shrink: 0;
  color: inherit;
  text-decoration: none;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0;
  box-sizing: inherit;

  &:hover {
    text-decoration: underline;
  }
`

export const StyledSearchRoot: typeof styled.div = styled.div`
  position: relative;
  margin: 0.6em 0;
`

const POPVER: string = css`
  max-height: 80vh;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  position: absolute;
  z-index: 2;
  right: 0;
  top: 100%;
  margin-top: 0.5em;
  width: 80vw;
  max-width: 30em;
  box-shadow: 0 0 5px 0;
  padding: 1em;
  border-radius: 2px;
  background: ${({theme}) => theme.background};
`
export const StyledSearchResult: typeof SearchResult = styled(SearchResult)`
  display: ${props => (props.show ? `block` : `none`)};
  ${POPVER}
`
