// @flow

import styled, {css} from 'styled-components'
import {Search} from '@styled-icons/fa-solid'

import type {ComponentType} from 'react'

export const Container: typeof styled.form = styled.form`
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  margin-bottom: 0;
`

const OPEN: string = css`
  width: 10em;
  background: ${({theme}) => theme.background};
  cursor: text;
  margin-left: -1.6em;
  padding-left: 1.6em;
`

const CLOSED: string = css`
  width: 0;
  background: transparent;
  cursor: pointer;
  margin-left: -1em;
  padding-left: 1em;
`

export const Input: ComponentType<{hasFocus: boolean}> & typeof styled.input = styled.input.attrs({
  type: 'text',
  placeholder: 'Search',
  'aria-label': 'Search',
})`
  outline: none;
  border: ${props => (props.hasFocus ? 'auto' : 'none')};
  font-size: 1em;
  transition: 100ms;
  border-radius: 2px;
  color: ${props => props.theme.foreground};
  ::placeholder {
    color: ${props => props.theme.faded};
  }
  ${props => (props.hasFocus ? OPEN : CLOSED)}
`

export const SearchIcon: typeof Search = styled(Search)`
  width: 1em;
  margin: 0.3em;
  color: ${({theme}) => theme.foreground};
  pointer-events: none;
`
