// @flow

import React from 'react'
import {connectSearchBox} from 'react-instantsearch-dom'

import {Container, Input, SearchIcon} from './styles'

import type {Props} from './types'

const Main: (props: Props) => React$Node = (props: Props) => {
  const {refine, currentRefinement, onFocus, onBlur, hasFocus} = props

  return (
    <Container>
      <Input
        onChange={e => refine(e.target.value)}
        value={currentRefinement}
        onFocus={onFocus}
        onBlur={onBlur}
        hasFocus={hasFocus}
      />
      <SearchIcon />
    </Container>
  )
}

export default connectSearchBox(Main)
